// import guid from "generate-unique-id";
import React, { useEffect, useState, useRef, useCallback } from 'react';
import axios from 'axios';
import { Context as FormContext } from '../../context/FormContext';
import { Context as DatesContext } from '../../context/DatesContext';
import { addYears, formatWithOptions } from 'date-fns/fp';

import { useContext } from 'react';

import { FormInput } from './FormInput';
import { es } from 'date-fns/locale';

import { areIntervalsOverlapping } from 'date-fns';

export const EditForm = () => {
	// const id = useId();
	const {
		state: { calendar: { intervalRentedArray } },
		addIntervalToArrayRentedMsg,
		editingMsg,
		sendIntervalForSocket
	} = useContext(DatesContext);

	const {
		startMsg,
		endMsg,
		phoneMsg,
		nameMsg,
		emailMsg,
		roomMsg,
		priceMsg,
		editFormMsg,
		nightsNumberMsg
	} = useContext(FormContext);
	const { intervalRented } = useContext(FormContext).state;

	const { name, email, phone, room, start, end, nights, price, priceNights, id, width } = intervalRented;
	console.log(width);
	const handleSubmit = async (e) => {
		e.preventDefault();

		// const HOST = window.location.origin.replace(/^http/, 'ws');
		// const socketUrl = `${HOST}/wss`;
		// const ws = new WebSocket(socketUrl);
		const ws = new WebSocket(`ws://localhost:8000/wss`);
		// setData(data);

		ws.onopen = async () => {
			// console.log('start, end, id');
			// const { id } = intervalRented;
			const IsOverLappingg = intervalRentedArray.some((interval) => {
				if (interval.id !== id) {
					return (
						areIntervalsOverlapping(
							{ start: new Date(interval.end), end: new Date(interval.start) },
							{ start: new Date(start), end: new Date(end) }
						) && `${interval.room}` === room.toString()
					);
				}
			});

			if (!IsOverLappingg) {
				console.log(id);

				const res = await axios.put(`http://localhost:8000/rented/${id}	`, {
					id: id,
					start: formatWithOptions({ locale: es }, 'yyyy/MM/dd')(start),
					room: room.toString(),
					end: formatWithOptions({ locale: es }, 'yyyy/MM/dd')(end),
					name: name,
					email: email,
					phone: phone,
					nights: nights.toString(),
					price: price
				});
				console.log(res);
				const dat2 = JSON.parse(res.config.data);

				// const { $oid: id } = res.data.insertedId;

				const data = {
					...dat2,
					id,
					isOverLappingg: IsOverLappingg
				};

				ws.send(JSON.stringify(data));
			}
			ws.onmessage = (msg) => {
				console.log(msg);
				const { data } = JSON.parse(msg.data);
				const superData = JSON.parse(data);

				if (!superData.isOverLappingg) {
					console.log(superData);

					editingMsg({
						...superData,
						start: new Date(superData.start),
						end: new Date(superData.end)
					});
				}

				ws.close();
			};
		};
		editFormMsg(false);
	};

	return (
		<div style={{ width: '100%', backgroundColor: 'blue', padding: '20px' }}>
			<form noValidate autoComplete="off" onSubmit={handleSubmit}>
				<div style={{ display: 'flex', justifyContent: 'space-around' }}>
					<div>
						<FormInput
							value={start}
							handleChange={(newVal) => {
								startMsg(newVal);
							}}
							type="date"
							label="Desde"
						/>

						<FormInput
							value={end}
							handleChange={(newVal) => {
								endMsg(newVal);
							}}
							type="date"
							label="Hasta"
						/>
						<FormInput
							value={nights}
							handleChange={(e) => nightsNumberMsg(e.target.value, e)}
							type="number"
							label="Noches"
						/>
						<FormInput
							value={phone}
							handleChange={(e) => phoneMsg(e.target.value)}
							type="text"
							label="Horario de Llegada"
						/>
					</div>
					<div>
						<FormInput
							value={room}
							handleChange={(e) => roomMsg(e.target.value)}
							type="text"
							label="Habitacion"
						/>
						<FormInput
							value={name}
							handleChange={(e) => nameMsg(e.target.value)}
							type="text"
							label="Nombre"
						/>
						<FormInput
							value={email}
							handleChange={(e) => emailMsg(e.target.value)}
							type="text"
							label="Email"
						/>
						<FormInput
							value={phone}
							handleChange={(e) => phoneMsg(e.target.value)}
							type="text"
							label="Telefono"
						/>
					</div>
					<div>
						<FormInput
							value={price}
							handleChange={(e) => priceMsg(e.target.value)}
							type="text"
							label="Precio"
						/>
						<FormInput
							value={priceNights}
							handleChange={(e) => nameMsg(e.target.value)}
							type="text"
							label="Total"
						/>
					</div>

					<div />
				</div>

				<button type="submit">SAVE </button>
			</form>
		</div>
	);
};
