import React, { useState } from 'react';
import './App.css';
import { ContainerContainer } from './components/Container/ContainerContainer';
import { Provider } from './context/ThumbContext';
import { Provider as DatesProvider } from './context/DatesContext';
import { Provider as FormProvider } from './context/FormContext';
import { Provider as IdProvider } from './context/IdContext';
import { HTML5Backend } from 'react-dnd-html5-backend';
// import { DndProvider } from 'react-dnd';
import { TouchBackend } from 'react-dnd-touch-backend';
// import HTML5Backend from 'react-dnd-html5-backend';
// import TouchBackend from 'react-dnd-touch-backend';
import { MultiBackend } from 'react-dnd-multi-backend';
// import CustomPreview from './CustomPreview';
import { HTML5toTouch } from 'rdndmb-html5-to-touch';
import { DndProvider, TouchTransition, MouseTransition } from 'react-dnd-multi-backend';
export default function App() {
	const HTML5toTouch = {
		backends: [
			{
				id: 'html5',
				backend: HTML5Backend,
				transition: MouseTransition
			},
			{
				id: 'touch',
				backend: TouchBackend,
				options: { enableMouseEvents: true },
				preview: true,
				transition: TouchTransition
			}
		]
	};

	return (
		<IdProvider>
			<FormProvider>
				<DatesProvider>
					<Provider>
						<DndProvider options={HTML5toTouch}>
							<ContainerContainer />
						</DndProvider>
					</Provider>
				</DatesProvider>
			</FormProvider>
		</IdProvider>
	);
}
